.product-img-bx {
  position: relative;
  overflow: hidden;
}

.product-img-bx .product-img {
  text-decoration: none;
  z-index: 200;
}

.product-img-bx .product-img > img {
  width: 100%;
  height: 100%;
  width: 200px;
  height: 200px;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transition: all .5s ease-in-out;
  transition: all .5s ease-in-out;
}

.add-to-cart {
  background: -webkit-gradient(linear, left top, right top, from(#373b44), to(#4286f4));
  background: -webkit-linear-gradient(left, #373b44, #4286f4);
  background: linear-gradient(to right, #373b44, #4286f4);
  color: #fff;
  z-index: 500;
  outline: none;
  border: none;
  padding: .5rem;
  cursor: pointer;
  -webkit-transition: all .5s ease-in-out;
  transition: all .5s ease-in-out;
  font-size: 1rem;
}

.add-to-cart:hover {
  color: #000;
}

.title-cart {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 1rem 0;
}

.product-card {
  width: 200px;
  box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  text-decoration: none;
  -webkit-transition: all .5s ease-in-out;
  transition: all .5s ease-in-out;
}

.product-card:hover {
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3);
}

.product-card:hover button {
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
}

.product-card:hover .product-img > img {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.add-to-cart {
  position: absolute;
  bottom: 0;
  right: 0;
  -webkit-transform: translate(100%, 100%);
          transform: translate(100%, 100%);
}

.text {
  color: #1a1a1a;
}

.product-content {
  padding: 1rem;
}
