.hero-img {
  width: 1200px;
  max-width: 95%;
  margin: 2rem auto;
}

.hero-img img {
  width: 100%;
}
footer {
  background: #d4d5d6;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem 0 0.5rem 0;
  flex-direction: column;
  text-align: center;
  text-transform: uppercase;
}

footer p {
  font-weight: bold;
}

.form-group input {
  height: 2rem;
  outline: none;
  text-indent: 0.5rem;
  border: none;
  font-size: 1rem;
  padding: 0;
  margin: 0;
}

.form-group {
  display: flex;
  margin-bottom: 1rem;
}

.form-group button {
  height: 2rem;
  font-size: 1rem;
  border: none;
  outline: none;
  background: var(--mainBlack);
  color: #fff;
  padding: 0 0.8rem;
  cursor: pointer;
  -webkit-transform: translateX(-5px);
          transform: translateX(-5px);
  text-transform: uppercase;
}

.social-link p {
  margin-bottom: 0.5rem;
}

.social-link span {
  font-size: 1.2rem;
}

.social-link span:not(:last-of-type) {
  margin-right: 1rem;
}

.social-link span i {
  color: #5f5e6a;
  -webkit-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
  cursor: pointer;
}

.social-link span i:hover {
  color: #1a1a1a;
}
.cart-product {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  justify-items: center;
  margin-top: 2rem;
  align-items: center;
  padding: 1rem 0;
  border-bottom: .01rem solid #d4d5d6;
}


.cart-product-img img {
  width: 5rem;
}

.cart-product-quantity button {
  width: 2rem;
  height: 2rem;
  display: flex;
  background: #d4d5d6;
  color: #1a1a1a;
  justify-content: center;
  align-items: center;
  border: none;
  outline: none;
  flex-direction: row;
}

.cart-product-quantity button:nth-child(2) {
  background: #1a1a1a;
  color: #fff;
}

.cart-product-quantity button:not(:nth-child(2)):active {
  background: #1a1a1a;
  color: #fff;
  cursor: pointer;
}

.cart-product-quantity button:not(:nth-child(2)):hover {
  cursor: pointer;
}

.cart-product-quantity {
  display: flex;
}

.remove-btn:hover {
  color: #d4d5d6;
}

.remove-btn {
  cursor: pointer;
}
.cartHeader {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  justify-items: center;
  margin-top: 3rem;

}

.cartHeader p {
  font-size: 1.5rem;
  font-weight: bold;
}

@media (max-width:576px) {
  .cartHeader p {
    font-size: 1.2rem
  }
}
.empty-cart-head {
  text-align: center;
  margin: 5rem 0 6rem 0;
  display: flex;
  justify-content: center;
  text-transform: uppercase;

}

.cart-head-text {
  position: relative;
}

.cart-count {
  font-size: 15rem;
  position: absolute;
  top: -110px;
  text-align: center;
  width: 100%;
  font-weight: bold;
  left: 0;
  color: #d4d5d6;
  z-index: -1;
  text-align: center;

}

@media(max-width:768px) {
  .cart-count {
    font-size: 10rem;
    top: -60px;
  }

  .empty-cart-head {
    margin: 4rem 0 5rem 0;
  }
}
.empty-cart-title {
  text-transform: uppercase;

}

.empty-content {
  margin: 4rem auto 0 auto;
  position: relative;
}


.empty-content p {
  font-size: 2rem;
  position: relative;
  margin-bottom: 1rem;
}

.empty-content h1 {
  font-size: 3.5rem;

  margin-bottom: 5rem;
}

@media (max-width:768px) {
  .empty-content p {
    font-size: 1.5rem;
  }

  .empty-content h1 {
    font-size: 3rem;
    margin-bottom: 5rem;
  }


  .empty-content p::before {
    font-size: 10rem;
  }
}

.empty-content p::before {
  content: "\f218";
  font-family: 'Font Awesome\ 5 Free';
  font-size: 15rem;
  position: absolute;
  top: 0;
  font-weight: bold;
  left: 0;
  color: #d4d5d6;
  z-index: -1;
}
.total-values {
  margin: 3rem 0;
  text-transform: uppercase;
  text-align: right;
  font-size: 1.5rem;
}

@media (max-width:768px) {
  .total-values {
    font-size: 1.1rem;
  }
}

.total-content>div:not(:last-of-type) {
  margin-bottom: .5rem;
}
.error {
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
}

.error-content {
  margin: 4rem auto 0 auto;
  width: 1200px;
  max-width: 95%;
}

.error-content p {
  font-size: 2rem;
  position: relative;
  margin-bottom: 1rem;
}

.error-content p::before {
  content: "404";
  font-size: 15rem;
  position: absolute;
  top: 0;
  font-weight: bold;
  left: 0;
  color: #d4d5d6;
  z-index: -1;
}

@media (max-width: 768px) {
  .error-content p::before {
    font-size: 10rem;
  }
}

.error-content h1 {
  font-size: 3.5rem;
  margin-bottom: 5rem;
}

@media (max-width: 768px) {
  .error-content p {
    font-size: 1.5rem;
  }
  .error-content h1 {
    font-size: 3rem;
    margin-bottom: 3rem;
  }
}

